<script>
  import {swiperAnimateCache,swiperAnimate} from "../../static/js/swiper.animate1.0.3.min.js"
export default {
  name: 'HelloWorld',
  data () {
    return {
      lastTime: new Date("2020/09/26 17:00:00").getTime(),
      hours:0,
      minutes:0,
      seconds:0,
      playFlag:false,
      days:0,
      swiperOptions:{
        direction:"vertical",
        initialSlide:0,
        effect: 'fade',
        loop:false,
        observe:true,
        observeParents: true,
        on:{
          init:function(swiper){
            console.log(11111)
            swiperAnimateCache(swiper);
            swiperAnimate(swiper);
          },
          slideChangeTransitionStart:function(){
          },
          slideChangeTransitionEnd:function(){
          },
          slideChange:function(swiper){

            swiperAnimate(swiper);
          },
        },
      }
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper
    }
  },
  mounted() {
    // 倒计时
    this.daojishi()
    setTimeout(()=>{
      let audio = document.getElementsByTagName('audio')[0];
      audio.addEventListener('canplay', () => {
        audio.play();
        this.playFlag = !audio.paused;
      })
    },500)
    // this.$axios.post("http://wx79821b4316595b0e.liveapi.xmainc.com/api/index/realApiUrl",{
    //   server: "LiveUser.getJsConfig",
    //   app_id: "wx79821b4316595b0e",
