    //   guest_token: "5bde96191f46c3e79d3094ddc1fd30a9",
    //   guest_id: "42"
    // }).then(res=>{
    //   console.log(res)
    //   res.data.data.jsApiList = ['onMenuShareTimeline']
    //   wx.config(res.data.data)
    //   wx.ready(function () {
    //     console.log(111)
    //     wx.onMenuShareTimeline({
    //       title: '送你一张优惠券，马上领取',
    //       link: 'https://m.jszhwlpt.com/',  // 可以分享别的页面 但要在同域名
    //       imgUrl: '/static/images/9.jpg',
    //       trigger: function (res) {},
    //       success: function (res) {}
    //     });
    //   })
    // })
    // this.swiper.slideTo(3, 1000, false)
  },
  methods:{
    play:function(){
      let audio = document.getElementsByTagName('audio')[0];
      this.playFlag = audio.paused;
      if(this.playFlag){
        document.getElementsByTagName("audio")[0].play();
      }
      else {
        document.getElementsByTagName("audio")[0].pause();
      }
    },
    daojishi:function () {
      let time = new Date().getTime(),lastTime =this.lastTime,t = (lastTime - time)/1000;
      t = Math.round(t);
      let days = Math.floor(t / (24 * 60 * 60));
      t = t - days * 24 * 3600;
      let hours = Math.floor(t/3600);
      t = t - hours * 3600;
      let minutes = Math.floor(t/60),seconds = t - minutes * 60;
      this.hours = hours;
      this.minutes = minutes;
      this.days = days;
      this.seconds = seconds;
      setTimeout(()=>{
        this.daojishi()
      },1000)
    }
  }
}
</script>

